import React from "react";
import style from "./index.module.scss";

import clsx from "clsx";

export default function Modal({ isOpen, onClose, className, children, noCloseButton }) {
  return (
    isOpen && (
      <div className={style.wrapper}>
        <div className={style.modalWrapper}>
          {!noCloseButton && (
            <div onClick={onClose} className={style.btnCross}>
              &times;
            </div>
          )}

          <div className={clsx(style.contentWrapper, className)}>{children}</div>
        </div>
      </div>
    )
  );
}
