import React from "react";
import style from "./index.module.scss";

import clsx from "clsx";

import Modal from "@components/Modal";

import Boy from "@assets/enquiry/boy.svg";
import BgLeft from "@assets/enquiry/bg_left.svg";

export default function EnquiryModal({ children, onClose }) {
  return (
    <Modal className={style.wrapper} isOpen={true} onClose={onClose}>
      <div className={clsx(style.graphic, style.boy)}>
        <img src={Boy} alt="" />
      </div>
      <div className={clsx(style.graphic, style.bgLeft)}>
        <img src={BgLeft} alt="" />
      </div>
      {children}
    </Modal>
  );
}
